body {
    padding: 0px;
    margin: 0px;
    font-size: 10px;
}
.header {
    background: #a1ffa3;
    justify-content: space-between;
    align-items: center;
    padding: 0 48px;
    display: flex;
}

.header ul {
    justify-content: space-between;
    gap: 1.6rem;
    list-style-type: none;
    display: flex;
    align-items: center;
    font-size: 1rem;
}

.cart-icon {
    width: 26px;
}